<template>
  <div>
    <div>
      <el-link
        :underline="false"
        type="primary"
        style="
          font-size: 16px;
          cursor: pointer;
          float: right;
          margin-bottom: 10px;
          color: ;
        "
        @click="add"
        ><i class="iconfont iconxinjian" style="padding-right: 5px"></i
        >新增</el-link
      >
    </div>

    <!-- <div style="margin-bottom:10px;">
        <span
          >已选择<span style="color: rgb(0, 161, 145); padding: 0 5px">{{
            tableSelection.length
          }}</span
          >项</span
        >
        <el-link
          class="table_link"
          type="danger"
          v-if="tableSelection.length"
          :underline="false"
          @click="handleMultiDelete"
          style="margin: -5px 10px 0px 10px"
          >批量删除</el-link
        >
      </div> -->
    <el-table
      v-loading="loading"
      :data="tableData"
      size="small"
      border
      :height="height - 120"
      style="
        width: 100%;
        text-align: center;
        line-height: 18px;
        overflow-y: auto;
      "
    >
      <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
      <el-table-column
        label="表情包"
        align="center"
        prop="url"
        show-overflow-tooltip
      >
        <template slot-scope="{ row }">
          <el-image
            style="width: 100px; height: 100px"
            :src="row.url"
            fit="cover"
          ></el-image>
        </template>
      </el-table-column>
      <el-table-column label="名字 " align="center" prop="name">
      </el-table-column>

      <el-table-column label="是否启用" align="center" prop="status">
        <template slot-scope="{ row }">
          {{ row.status ? "是" : "否" }}
        </template>
      </el-table-column>

      <el-table-column label="操作" align="center" width="140">
        <template slot-scope="{ row }">
          <i class="el-icon-edit" @click="edit(row)"></i>
          <i
            class="el-icon-delete"
            style="margin-left: 10px"
            @click="handleDelete(row)"
          ></i>
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>

    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogFormVisible"
      width="650px"
      :close-on-click-modal="false"
      :append-to-body="true"
      @closed="resetForm"
    >
      <el-form ref="ruleForm" size="mini" :model="form">
        <el-form-item label="图片:">
          <upload @success="handleSuccess" ref="myupload" :url="url"></upload>
        </el-form-item>

        <el-form-item label="名字:" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="是否启用:">
          <el-switch
            v-model="form.status"
            active-color="#13ce66"
            inactive-color="#ccc"
          >
          </el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm" size="mini">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')" size="mini"
          >提 交</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import upload from "@/pages/common/upload.vue";

export default {
  name: "footage_cop",
  components: {
    upload,
  },
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loading: false,
      tableData: [],
      dialogFormVisible: false,
      dialogTitle: "新增",
      form: {
        name: "",
        status: false,
        url: null,
      },
      url: null,
    };
  },
  created() {},
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$reqGet("/community/mood").then((res) => {
        if (res.code == 200) {
          this.tableData = res.data;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let url = this.form.id
            ? "/community/editMoods"
            : "/community/addMoods";
          this.$reqPost(url, {
            ...this.form,
          }).then((res) => {
            if (res.code == 200) {
              this.dialogFormVisible = false;
              this.getData();
            } else {
              this.$message.error(res.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.form.name = "";
      this.form.status = false;
      this.form.url = null;
      this.url = null;
      this.$refs.myupload.handleRemove();
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },

    add() {
      this.dialogTitle = "管理";
      this.dialogFormVisible = true;
    },
    edit(row) {
      this.form = {
        ...row,
      };
      this.url = row.url;
      this.dialogFormVisible = true;
    },
    handleDelete(row) {
      this.$confirm("确定删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        closeOnClickModal: false,
      }).then(() => {
        let id = row.id;
        this.$reqPost("/community/delMoods", {
          id,
        }).then((res) => {
          if (res.code == 200) {
            this.getData();
          } else {
            this.$message.error(res.msg);
          }
        });
        console.log(row);
      });
    },
    handleSuccess(res) {
      console.log(res);
      this.form.url = res.url;
    },
  },
};
</script>
<style scoped></style>
